
.container{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;  
    align-content: center;
}
.card {
    margin: 20px;
    width: 25rem;
    height: 24rem;
    background-color:mediumseagreen;
    border-radius: 15px;
    box-shadow: 12px 12px 2px 1px rgba(27, 27, 27, 0.6);
    border: 2px solid palegreen;
    color:rgb(247, 185, 72);
  }
  
.textos {
    margin: 20px;
    justify-content: center;

}


.info{
    color: whitesmoke;
}

.close {
    display: flex;
    justify-content: flex-end;
    margin-right: 4px;
    margin-top: 4px;
}

.btn {
    background-color: lightgoldenrodyellow;
    justify-content: flex-end;
    color: mediumseagreen;
    border-radius: 50%;
    font-weight: bold;
    border: 2px solid palegreen;
}

.btn:hover {
    background-color: mediumseagreen;
    color: whitesmoke;
}

.btn:focus {
    outline: none;
}
